import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';

import { DependencyDB } from '../dependency-db'
import { DependencyService } from '../dependency.service'

import { FrontRepoService, FrontRepo } from '../front-repo.service'

import { Router, RouterState, ActivatedRoute } from '@angular/router';

export interface dependencyDummyElement {
}

const ELEMENT_DATA: dependencyDummyElement[] = [
];

@Component({
	selector: 'app-dependency-presentation',
	templateUrl: './dependency-presentation.component.html',
	styleUrls: ['./dependency-presentation.component.css'],
})
export class DependencyPresentationComponent implements OnInit {

	// insertion point for declarations

	displayedColumns: string[] = [];
	dataSource = ELEMENT_DATA;

	dependency: DependencyDB;

	// front repo
	frontRepo: FrontRepo
 
	constructor(
		private dependencyService: DependencyService,
		private frontRepoService: FrontRepoService,
		private route: ActivatedRoute,
		private router: Router,
	) {
		this.router.routeReuseStrategy.shouldReuseRoute = function () {
			return false;
		};
	}

	ngOnInit(): void {
		this.getDependency();

		// observable for changes in 
		this.dependencyService.DependencyServiceChanged.subscribe(
			message => {
				if (message == "update") {
					this.getDependency()
				}
			}
		)
	}

	getDependency(): void {
		const id = +this.route.snapshot.paramMap.get('id');
		this.frontRepoService.pull().subscribe(
			frontRepo => {
				this.frontRepo = frontRepo

				this.dependency = this.frontRepo.Dependencys.get(id)

				// insertion point for recovery of durations
			}
		);
	}

	// set presentation outlet
	setPresentationRouterOutlet(structName: string, ID: number) {
		this.router.navigate([{
			outlets: {
				github_com_fullstack_lang_gonggooglecharts_go_presentation: ["github_com_fullstack_lang_gonggooglecharts_go-" + structName + "-presentation", ID]
			}
		}]);
	}

	// set editor outlet
	setEditorRouterOutlet(ID: number) {
		this.router.navigate([{
			outlets: {
				github_com_fullstack_lang_gonggooglecharts_go_editor: ["github_com_fullstack_lang_gonggooglecharts_go-" + "dependency-detail", ID]
			}
		}]);
	}
}
